body {
  background-color: rgb(250, 250,250);
}

h2{
  font-family: 'Courier New', Courier, monospace;
  align-content: 'flex-start';
  display: flex;  
  margin: auto;
}

.amar{
  font-family: 'Courier New', Courier, monospace;
}

.container {
  display: flex;
  align-items: center;
  flex-direction: column;
}
.image {
  width: 370px;
  margin-top: 50px;
}

@media only screen and (max-width: 770px) {
  .container {
    margin: 0 10%;
  }

  .image {
    width: 100%;
  }
}
